import PropTypes from 'prop-types';
import React from 'react';
import { useFormikContext } from 'formik';
import { useTimeoutFn } from 'react-use';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { WebsiteEditForm } from '../../../../UI';

const ImmediateSubmit = ({ delay }) => {
    const formik = useFormikContext();

    useTimeoutFn(() => formik.submitForm(), delay);

    return null;
};

ImmediateSubmit.propTypes = {
    delay: PropTypes.number,
};

export function HomepageEdit() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();

    return (
        <WebsiteEditForm
            title={t('Page.Edit.HomePage.heading')}
            website={website}
            initialValues={{ homePage: page._id }}
            backPath={generateUrl('../..')}
        >
            <ImmediateSubmit delay={0} />
        </WebsiteEditForm>
    );
}
